@-webkit-keyframes animParticle{
  from{
    -webkit-transform:translateY(0px);
    transform:translateY(0px)
  }

  to{
    -webkit-transform:translateY(-1500px);
    transform:translateY(-1500px)
  }
}

@keyframes animParticle{
  from{
    -webkit-transform:translateY(0px);
    transform:translateY(0px)
  }

  to{
    -webkit-transform:translateY(-1500px);
    transform:translateY(-1500px)
  }
}

