.ExamComp{
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  margin-top: 0;
}

.ExamComp input[type="radio"]:after {
  width: 24px;
  height: 24px;
  border-radius: 24px;
  cursor: pointer;
  position: relative;
  background-color: #535353;
  content: "";
  display: inline-block;
  visibility: visible;
  border: 2px solid white;
}

input[type="radio"]:checked:after {
  width: 24px;
  height: 24px;
  border-radius: 24px;
  cursor: pointer;
  position: relative;
  background-color: rgba(55, 255, 255, 0.5);
  content: "";
  display: inline-block;
  visibility: visible;
  border: 2px solid white;
}

.optionDiv{
  width:100%;
}

.card{
  box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
